<template>
  <wm-dialog v-model:show="infoShow" :title="title" width="720px" bottom="40px" @close="close()">
    <wm-main>
      <wm-tabs :columns="tabs">
        <!--  -->
        <template #base>
          <wm-table-form>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-cascader v-model:value="form.fid" :options="menusAll" clearable></wm-cascader>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.title" placeholder="" maxlength="16"></wm-input>
              </td>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.en" placeholder="" maxlength="16"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-input v-model:value="form.url" placeholder="" maxlength="32"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-input v-model:value="form.controller" placeholder="" maxlength="32"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.ico" placeholder="" maxlength="32"></wm-input>
              </td>
              <td class="label"></td>
              <td>
                <wm-input v-model:value="form.sort" placeholder=", : 0, 1, 2" maxlength="32"></wm-input>
              </td>
            </tr>
            <tr>
              <td class="label"></td>
              <td colspan="3">
                <wm-input v-model:value="form.controller" type="textarea" :height="'80px'" placeholder="" maxlength="32"></wm-input>
              </td>
            </tr>
          </wm-table-form>
        </template>
        <!--  End -->
        <!--  -->
        <template #action>
          <wm-table-form>
            <tr class="title">
              <td></td>
              <td></td>
              <td></td>
              <td class="action">
                <wm-button effect="text" type="primary" @click="actionAdd()"></wm-button>
              </td>
            </tr>
            <tr v-for="(v,k) in form.action" :key="k">
              <td>
                <wm-input v-model:value="v.name" maxlength="32"></wm-input>
              </td>
              <td>
                <wm-input v-model:value="v.action" maxlength="32"></wm-input>
              </td>
              <td>
                <wm-input v-model:value="v.perm" maxlength="32"></wm-input>
              </td>
              <td class="action">
                <wm-button effect="text" type="danger" @click="actionRemove(k)"></wm-button>
              </td>
            </tr>
          </wm-table-form>
        </template>
        <!--  End -->
      </wm-tabs>
    </wm-main>
    <template #bottom>
      <wm-button effect="dark" type="primary" height="40px" @click="submit()"> </wm-button>
    </template>
  </wm-dialog>
</template>

<style lang="less" scoped>
.wm-table_form .label{width: 70px;}
.wm-table_form .action{width: 60px; text-align: center;}
</style>

<script lang="ts">
import { Options, Vue } from 'vue-class-component';
import { useStore } from 'vuex';
/* UI */
import Ui from '@/library/ui'
import Request from '@/library/request'
/*  */
import wmMain from '@/components/container/main.vue'
import wmDialog from '@/components/dialog/index.vue'
import wmInput from '@/components/form/input/index.vue'
import wmButton from '@/components/form/button/index.vue'
import wmCascader from '@/components/form/cascader/index.vue'
import wmTableForm from '@/components/table/form.vue'
import wmTabs from '@/components/tabs/index.vue'

@Options({
  components: { wmMain, wmDialog, wmInput, wmButton, wmCascader, wmTableForm, wmTabs },
  props: {
    show: {type: Boolean, default: false},        // 
    type: {type: String, default: ''},            // : add, edit
    data: {default: []},                          // 
  }
})
export default class MenusAdd extends Vue {
  // 
  show!: boolean;
  type!: string;
  data!: any;
  // 
  store: any = useStore();
  state: any = this.store.state;
  // 
  infoShow: boolean = false;
  title: string = '';
  // Tabs
  tabs: Array<any> = [
    {label: '', value: 'base', slot: 'base', checked: true},
    {label: '', value: 'action', slot: 'action'},
  ];
  // 
  form: any = {id:0, fid: [], title: '', en: '', ico: '', sort: 0, url: '', controller: '', remark:'', action:[]}
  // 
  fid: Array<any> = [];
  menusAll: any = [];

  /*  */
  created(): void {
    this.$watch('show', (val:boolean)=>{
      this.infoShow = val;
    }, { deep: true });
    this.$watch('data', (v:any)=>{
      this.title = this.type=='add'?'':'';
      // 
      this.form.id = v.id || 0;
      this.form.title = v.title || '';
      this.form.en = v.en || '';
      this.form.ico = v.ico || '';
      this.form.sort = v.sort || 0;
      this.form.url = v.url || '';
      this.form.controller = v.controller || '';
      this.form.remark = v.remark || '';
      this.form.action = v.action || [];
      // Fid
      if(typeof v.fid != 'undefined') this.getFid(v.fid);
      else this.form.fid = [];
    }, { deep: true });
  }
  /*  */
  public mounted(): void {
    if(this.state.token){
      this.getMenus();
    }
  }

  /* Fid */
  private getFid(fid: number): void {
    // 
    for(let v1 of this.menusAll) {
          if(v1.value==fid) { this.form.fid = [v1.value]; break; }
          if(!v1.children) continue;
          // 
          for(let v2 of v1.children) {
            if(v2.value==fid) { this.form.fid = [v1.value, v2.value]; break; }
            if(!v2.children) continue;
            // 
            for(let v3 of v2.children) {
              if(v3.value==fid) { this.form.fid = [v1.value, v2.value, v3.value]; break; }
              if(!v3.children) continue;
              // 
              for(let v4 of v3.children) {
                if(v4.value==fid) { this.form.fid = [v1.value, v2.value, v3.value, v4.value]; break; }
              }
            }
          }
        }
  }

  /* - */
  actionAdd(): void {
    const action: Array<any> = this.form.action;
    if(action.length>0) action.push({name:'', action:'', perm:action[action.length-1].perm*2});
    else this.form.action = [{name:'', action:'', perm:1}];
  }
  /* - */
  actionRemove(k: number): void {
    const action: Array<any> = this.form.action;
      action.splice(k, 1);
  }

  /*  */
  verify(form: any): any {
    if(!form.title || form.title.length<2) return Ui.Toast('2');
    return form;
  }

  /*  */
  submit(): void {
    // 
    const form = this.verify(this.form);
    if(!form) return;
    // 
    const load: any = Ui.Loading();
    Request.Post('sys_menus/save', {
      token: this.state.token,
      data: form,
    }, (res:any)=>{
      load.clear();
      const d: any = res.data;
      Ui.Toast(d.msg);
      if(d.code==0) this.getMenus();
      this.$emit('submit', d.code==0);
    });
  }

  /*  */
  getMenus(): void {
    Request.Post('sys_menus/get_menus_all', {
      token: this.state.token,
    }, (res:any)=>{
      const d: any = res.data;
      if(d.code==0) this.menusAll = d.data;
      else Ui.Toast(d.msg);
    });
  }

  /*  */
  close(): void {
    this.$emit('update:show', false);
  }

}
</script>